  font-size: 22px;
  font-family: 'hn-b';
  position: relative;
  padding-left: 25px;
}

@media (max-width: 767px) {
  .back-btn a {
    font-size: 20px;
    padding-left: 20px;
  }
}

.back-btn a:before {
  position: absolute;
  content: " ";
  width: 18px;
  left: 0;
  top: 6px;
  background-image: url(../img/back.png);
  background-repeat: no-repeat;
  background-size: contain;
  height: 18px;
}

@media (max-width: 767px) {
  .back-btn a:before {
    top: 8px;
    width: 15px;
    height: 15px;
  }
}

.back-btn a:hover {
  color: #27a6b5;
}

.back-btn a:hover:before {
  background-image: url(../img/back-hover.png);
}

