  border-bottom: 1px solid #fff;
  list-style: none;
  margin: 0 auto;
  padding: 0;
  position: relative;
  width: 96%;
}

.slider-maper li {
  display: inline-block;
  position: absolute;
  top: -32px;
  transform: translateX(-50%);
}

.slider-maper li a {
  color: #fff;
  display: block;
  height: 32px;
  position: relative;
}

.slider-maper li a:after {
  background-color: #fff;
  border-radius: 50%;
  bottom: -4px;
  content: '';
  height: 8px;
  left: 50%;
  position: absolute;
  transform: translateX(-50%);
  width: 8px;
}

.slider-maper li a:before {
  content: '';
  left: -50%;
  position: absolute;
  top: -50%;
  max-width: initial !important;
}

