    max-width: 80%;
  }
}

@media (max-width: 479px) {
  .quiz-text {
    max-width: 100%;
  }
}

.form-imgSubmit {
  margin-bottom: 20px;
}

.drag_drp {
  float: left;
  width: 45%;
  margin: 15px 2.5%;
  padding: 0;
  list-style-type: none;
}

@media (max-width: 767px) {
  .drag_drp {
    width: 100%;
    margin: 15px 0;
  }
}

.drag_drp li {
  float: left;
  width: 33.33%;
  border: 1px solid rgba(22, 28, 34, 0.7);
  display: block;
  position: relative;
}

.drag_drp li span {
  padding: 3px;
  display: block;
  height: 100%;
  width: 100%;
}

.drag_drp li span img {
  width: 100%;
  height: 100%;
  display: block;
}

.title_drg_drp {
  width: 100%;
  display: block;
  margin-bottom: 10px;
  font-size: 20px;
}

.ui-draggable-dragging {
  width: 100px !important;
  height: 100px !important;
  z-index: 99;
}

.time_out_cst {
  width: 100%;
  height: 5px;
  -webkit-border-radius: 50px;
  border-radius: 50px;
  margin-top: -5px;
  z-index: 99;
}

.modal_content_fill {
  padding: 10px;
}

#quiz_material_popup .close {
  position: absolute;
  right: 15px;
  top: 20px;
}

#quiz_material_popup .close:hover, #quiz_material_popup .close:focus {
  outline: none;
}

#quiz_material_popup .modal-header {
  border-bottom: none;
}

#single_line_answer_box {
  text-transform: uppercase;
  text-align: center;
  border-image: none;
}

.quiz_view .quiz-ans .box .width-50 .incorrect input:checked ~ em, .quiz_view .quiz-ans .box .width-50 .correct input:checked ~ em {
  margin: 0;
}

.enlarge-popup {
  position: absolute;
  right: 6px;
  top: 3px;
  color: #565b5f;
  font-size: 17px;
}

.institute-sec {
  padding-bottom: 50px;
}

.institute-sec .clearfix {
  clear: both;
  background: #eeeeef;
}

.institute-sec .bg-white {
  padding: 0;
}

.institute-sec .institute-block {
  margin-bottom: 50px;
  background: #fff;
}

